.education-section {
margin: auto;
  color: #fff;
  padding: 40px 20px;
}

.education-title {
  text-align: center;
  font-size: 2.5rem;
  color: #00bcd4;
  margin-bottom: 1rem;
}

.education-cards {
  display: flex;
  flex-direction: row;   
  gap: 40px;
  max-width: 1000px;     
  margin: 0 auto;
  position: relative;
  padding-left: 0;       
  justify-content: center; 
}



.education-card {
  display: flex;
  align-items: center;
  background: #222c3d;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 8px rgba(0, 188, 212, 0.2);
}

.education-logo {
  width: 80px;
  height: 80px;
  background: white;
  border-radius: 10px;
  padding: 10px;
  object-fit: contain;
  margin-right: 20px;
}

.education-details h3 {
  margin: 0;
  font-size: 1.2rem;
  color: #00d4ff;
}

.education-details p {
  margin: 5px 0 0;
  color: #ccc;
  font-size: 1rem;
}
